"use client"

import { useEffect, useRef, useState } from "react"
import { Search, Mic, Command } from "lucide-react"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import gsap from "gsap"

export interface SearchHeroProps {
    onSearch: (query: string) => void
    defaultValue?: string
}

export function SearchHero({ onSearch, defaultValue = "" }: SearchHeroProps) {
    const containerRef = useRef<HTMLDivElement>(null)
    const titleRef = useRef<HTMLHeadingElement>(null)
    const searchRef = useRef<HTMLDivElement>(null)
    const tagsRef = useRef<HTMLDivElement>(null)
    const [query, setQuery] = useState(defaultValue)

    useEffect(() => {
        const tl = gsap.timeline({ defaults: { ease: "power3.out" } })

        tl.fromTo(titleRef.current,
            { y: 50, opacity: 0 },
            { y: 0, opacity: 1, duration: 1, delay: 0.2 }
        )
            .fromTo(searchRef.current,
                { scale: 0.9, opacity: 0 },
                { scale: 1, opacity: 1, duration: 0.8 },
                "-=0.5"
            )
            .fromTo(tagsRef.current?.children || [],
                { y: 20, opacity: 0 },
                { y: 0, opacity: 1, duration: 0.5, stagger: 0.1 },
                "-=0.3"
            )
    }, [])

    const handleSearch = (e: React.FormEvent) => {
        e.preventDefault()
        if (query.trim()) {
            // Animate out
            gsap.to(containerRef.current, {
                y: -50,
                opacity: 0,
                duration: 0.5,
                onComplete: () => {
                    onSearch(query)
                }
            })
        }
    }

    return (
        <div ref={containerRef} className="relative z-10 flex flex-col items-center justify-center min-h-screen px-4 text-center">
            <h1
                ref={titleRef}
                className="text-5xl md:text-7xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-white via-indigo-200 to-indigo-400 mb-8 tracking-tight"
            >
                Discover Intelligence
            </h1>

            <div
                ref={searchRef}
                className="w-full max-w-2xl relative group"
            >
                <div className="absolute -inset-1 bg-gradient-to-r from-indigo-500 to-purple-600 rounded-2xl blur opacity-25 group-hover:opacity-50 transition duration-1000 group-hover:duration-200" />
                <form onSubmit={handleSearch} className="relative">
                    <div className="relative flex items-center bg-white/10 backdrop-blur-xl border border-white/20 rounded-xl shadow-2xl overflow-hidden">
                        <Search className="ml-4 h-6 w-6 text-indigo-200" />
                        <Input
                            value={query}
                            onChange={(e) => setQuery(e.target.value)}
                            className="h-16 border-0 bg-transparent text-lg text-white placeholder:text-white/50 focus-visible:ring-0 focus-visible:ring-offset-0"
                            placeholder="Ask anything..."
                        />
                        <div className="mr-4 flex items-center gap-2">
                            <Button size="icon" variant="ghost" className="text-white/70 hover:text-white hover:bg-white/10">
                                <Mic className="h-5 w-5" />
                            </Button>
                            <div className="hidden md:flex items-center gap-1 text-xs text-white/50 bg-white/5 px-2 py-1 rounded border border-white/10">
                                <Command className="h-3 w-3" />
                                <span>K</span>
                            </div>
                        </div>
                    </div>
                </form>
            </div>

            <div
                ref={tagsRef}
                className="mt-8 flex flex-wrap justify-center gap-3"
            >
                {["AI Research", "Deep Learning", "Neural Networks", "Quantum Computing"].map((tag) => (
                    <span
                        key={tag}
                        className="px-4 py-2 rounded-full bg-white/5 border border-white/10 text-sm text-indigo-200 cursor-pointer hover:bg-white/10 hover:border-indigo-500/50 transition-all duration-300"
                    >
                        {tag}
                    </span>
                ))}
            </div>
        </div>
    )
}
